.paragraphDiv {
  display: flex;
  flex-direction: column;
  text-align: left;
  box-sizing: border-box;
  padding-top: 0.5rem;
  margin-bottom: 1rem;
}

#phDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  margin-top: 1rem;
  height: 35rem;
}

#placeholder,
.placeholder {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
}

#dataPage {
  padding: 0% 5% 1%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.paragraphDiv p {
  display: flex;
  margin: 0;
}

#graphDiv,
#tableDiv {
  display: flex;
  justify-content: center;
}

table {
  box-sizing: border-box;
  padding: 1rem;
  text-align: left;
  font-size: 0.8rem;
}

th {
  background: #ccc;
  text-align: justify;
  vertical-align: middle;
  padding: 0.3rem;
}

td {
  padding: 2%;
}

tr:nth-child(even) {
  background: #ccc;
}

button {
  height: 2rem;
  place-self: center;
  margin: 1rem;
}

canvas {
  box-sizing: border-box;
}

.addImg {
  background-image: url(../app/img/flag.jpg);
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  text-align: left;
  box-sizing: border-box;
  padding-top: 0.5rem;
  margin-bottom: 1rem;
}

.loading {
  width: 10rem;
  height: 10rem;
  border: 5px solid #f3f3f3;
  border-top: 6px solid #9c41f2;
  border-radius: 100%;
  margin: auto;
  visibility: visible;
  animation: spin 1s infinite linear;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 370px) {
  #graphDiv {
    max-width: 250px;
    align-self: center;
  }

  .secondParDiv {
    background-size: inherit;
  }

  .loading {
    width: 3rem;
    height: 3rem;
    align-self: center;
  }

  #phDiv {
    width: 45rem;
  }

  table {
    font-size: 0.8rem;
  }
}
